@import url(https://fonts.googleapis.com/css2?family=Encode+Sans:wght@200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.login-page {
  width: 100vw;
  height: 100vh;
  display: flex;
}

.img-section {
  width: 50vw;
  height: 100%;
  background: linear-gradient(45deg, rgba(29,53,87,1) 0%, rgba(168,218,220,1) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.login-img {
  width: 30em;
}

.form-page {
  width: 50vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f6f5fc;
}

.login-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
  height: 29.5em;
  width: 23em;
}

.title-img {
  width: 16em;
}

.form {
  height: 12em;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  position: relative;
}

.form input {
  width: 80%;
  height: 35px;
  outline: none;
  border: none;
  border-radius: 5px;
  border-left: 3px solid #1d3557;
  padding-left: 6px;
  background-color: #f6f5fc;
  font-size: 1em;
  color: #1d3557;
  transition: all 0.3s ease;
}

.form input::-webkit-input-placeholder {
  color: #678788;
  font-weight: 400;
}

.form input::placeholder {
  color: #678788;
  font-weight: 400;
}

.email-input:focus ~ .underline-1 {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.password-input:focus ~ .underline-2 {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.underline {
  width: 78.7%;
  position: absolute;
  height: 1px;
  top: 67px;
  right: 38px;
  background-color: #1d3557;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: left;
          transform-origin: left;
  transition: all 0.35s ease;
}

.underline-2 {
  top: 132px;
}

.login-button {
  margin: 10px;
  width: 80%;
  height: 45px;
  font-size: 1em;
  font-weight: 600;
  outline: none;
  border: none;
  border-radius: 5px;
  background: linear-gradient(90deg, rgba(69,123,157,1) 0%, rgba(33,70,121,1) 100%);
  color: #f6f5fc;
  cursor: pointer;
}

.login-button:hover {
  background: #e65661;
}

.login-button:disabled {
  background: #e4ebeb;
  color: gray;
}

.header {
  height: 18vh;
  width: 100%;
  padding: 5px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background-color: #f6f5fc; */
  /* color: rgba(29,53,87,1); */
  color: #f6f5fc;
  background: linear-gradient(-45deg, rgba(29,53,87,1), rgb(29, 72, 122), rgb(44, 91, 145), rgb(49, 125, 153));
  background-size: 400% 400%;
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;
}

.header-img {
  width: 9em;
}

.header p {
  font-weight: 500;
}

.expense {
  padding-bottom: 4px;
  border-bottom: 1px solid #f6f5fc;
}

@-webkit-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.table {
  border-collapse: collapse;
  width: 90%;
  font-size: 0.95em;
}

.table thead th {
  width: 12.5em;
  height: 3em;
  color: #192e4d;
  border: 2px solid #192e4d;
}

.table tbody tr {
  border: 1px solid #67768d;
  transition: 0.2s ease-in-out;
}

.table tbody tr:hover {
  background-color: #d8e6fa;
}

.table tbody tr:last-child {
  border-bottom: 2px solid #192e4d;
}

.table tbody td {
  width: 12.5em;
  height: 6em;
  padding: 10px 5px;
  color: #0a1629;
  text-align: center;
  word-wrap: break-word;
  border: none;
}

.edit-button {
  margin-right: 6px;
  cursor: pointer;
  margin-top: 6px;
  color: #4fc544;
}

.remove-button {
  margin-left: 6px;
  cursor: pointer;
  margin-top: 6px;
  color: #df4b57;
}

.edit-button:hover {
  color: #27af1b;
}

.remove-button:hover {
  color: #d12634;
}

.wallet-form {
  width: 85%;
  background-color: #f6f5fc;
  border: none;
  border-radius: 10px;
  box-shadow: 4px 4px 10px #315486;
  color: #192e4d;
  margin: 50px 0;
  padding: 20px;
  display: flex;
  justify-content: space-evenly;
}

.cl {
  box-shadow: 4px 4px 10px #47cc7e;
}

.form-label {
  font-weight: 500;
  display: flex;
  grid-gap: 6px;
  gap: 6px;
}

.name-input {
  max-width: 6em;
}

.desc-input {
  max-width: 12em;
}

.form-label input, select {
  outline: none;
  border: none;
  border-bottom: 1px solid #192e4d;
  background-color: #f6f5fc;
  color: #192e4d;
}

.form-label input::-webkit-input-placeholder {
  font-size: 0.95em;
  color: #67768d;
}

.form-label input::placeholder {
  font-size: 0.95em;
  color: #67768d;
}

.form-button {
  width: 6em;
  padding: 2px;
  padding-top: 3px;
  background-color: #f6f5fc;
  color: #192e4d;
  font-weight: 500;
  outline: 0;
  border: 2px solid #192e4d;
  border-radius: 4px;
  cursor: pointer;
}

.form-button:hover {
  background-color: #192e4d;
  color: #f7f6fd;
}

.main-wallet {
  min-height: 100vh;
  width: 100%;
  background-color: #f6f5fc;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
  padding-bottom: 50px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

